<script>
    //秒换分秒
    function s_to_hs(S) {
        var h;
        var s;
        h = Math.floor(S / 60);
        s = Math.floor(S % 60);
        //将变量转换为字符串
        h = h+"";
        s = s+"";
        h = (h.length === 1) ? '0' + h : h;
        s = (s.length === 1) ? '0' + s : s;
        return h + "'" + s + '"';
    }

    export default {
        name: "photograph",
        data() {
            return {
                videoUrl: [
                    {url: "http://221.228.226.23/11/t/j/v/b/tjvbwspwhqdmgouolposcsfafpedmb/sh.yinyuetai.com/691201536EE4912BF7E4F1E2C67B8119.mp4"},
                    {url: "http://221.228.226.23/11/t/j/v/b/tjvbwspwhqdmgouolposcsfafpedmb/sh.yinyuetai.com/691201536EE4912BF7E4F1E2C67B8119.mp4"},
                    {url: "http://clips.vorwaerts-gmbh.de/big_buck_bunny.mp4"},
                    {url: "http://clips.vorwaerts-gmbh.de/big_buck_bunny.mp4"},
                    {url: "http://221.228.226.23/11/t/j/v/b/tjvbwspwhqdmgouolposcsfafpedmb/sh.yinyuetai.com/691201536EE4912BF7E4F1E2C67B8119.mp4"}
                ]
            }
        },
        methods: {
            playPause: function (x) {
                let video = document.getElementsByClassName("video")[x];
                if (video.paused) {
                    video.play()
                }
                else {
                    video.pause();
                }
                console.log(video.paused)
            },
            myFunction: function (x) {

                    var a = s_to_hs(document.getElementsByClassName("video")[x].duration);
                    var b = document.getElementsByClassName("text")[x];
                    b.innerHTML = a;

            }
        }
    }
</script>

