<template>
    <div>
        <h1>文学部</h1>
        <h2>{{this.$route.query.userId}}</h2>
        <button  @click="btn1">获取Local storage</button><br>
        <button  @click="btn3">获取cookies</button><br>
        <div id="result"></div><br>
        <div id="qwer"></div><br>
        <button @click="clear">清除storage和cookies</button>
    </div>
</template>

<script>
    function get (name) {
        var cookieName = encodeURIComponent(name) + "=",
            cookieStart = document.cookie.indexOf(cookieName),
            cookieValue = null;
        if (cookieStart > -1) {
            var cookieEnd = document.cookie.indexOf(";", cookieStart);
            if (cookieEnd == -1) {
                cookieEnd = document.cookie.length;
            }
            cookieValue = decodeURIComponent(document.cookie.substring(cookieStart + cookieName.length, cookieEnd))
        }
        return cookieValue;
    };

    export default {
        name: "literature",
        methods:{
            btn1:function () {
                document.getElementById("result").innerHTML = localStorage.getItem("can_use_webp");
            },
            clear:function () {
                localStorage.removeItem("name");
                document.cookie = "user=; expires=Thu, 01 Jan 1970 00:00:00 GMT";
                console.log("清除storage和cookies")
    },
            btn3:function () {
                document.getElementById("qwer").innerHTML = get("user");
            }
        }
    }
</script>

